#treeViewContainer {
    width: 250px;
    overflow: auto;
    white-space: nowrap;
    padding-bottom: 12px;
    border-right: #c3c3c3 0.3px solid;
}

ul#treeView, #treeView ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

#treeView li {
    padding-left: 15px;
}

#treeView span.no-caret {
    padding-left: 10px;
}

#treeView span {
    cursor: pointer;
    user-select: none;
    padding-top: 2px;
    padding-bottom: 1px;
}

#treeView .caret::before {
    content: "\25B6";
    color: #acacac;
    display: inline-block;
    margin-right: 3px;
    font-size: 0.5em;
    vertical-align: 3px;
}

#treeView .caret.down::before {
    transform: rotate(90deg);
}

#treeView .nested {
    display: none;
}
#treeView .highlight {
    background: #94bcb6;
    color: white;
}

#treeView .nested.active {
    display: block;
    padding-top: 2px;
    padding-bottom: 4px;
}
